import React, { useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import { useHistory } from 'react-router-dom';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import Select from '@material-ui/core/Select';
import DialogTitle from '@material-ui/core/DialogTitle';
import InputLabel from '@material-ui/core/InputLabel';
import FormControl from '@material-ui/core/FormControl';
import MenuItem from '@material-ui/core/MenuItem';
import firebase from '../Firebase';

import { SendMail } from './SendMail';

var generator = require('generate-password');

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

export default function NavBar() {
  const classes = useStyles();
  const history = useHistory();
  const [open, setOpen] = React.useState(false);

  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [contact, setContact] = useState("");
  const [role, setRole] = useState("");
  const [msg, setMsg] = useState("");
  const [password, setPassword] = useState("");


  const validEmail = (email) => {
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(email);
  }

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    registerUser();
    setOpen(false);
  };

  const navigate = (type) => {
    const path = '/' + type;
    history.push(path);
    return;
  }

  const disappear = () => {
    if(msg !== ""){
      setTimeout(() => setMsg(""), "");
    }
  }

  const setValue = (e, type) => {
    e.preventDefault();
    const val = e.target.value;
    if (type === "email") {
      setEmail(val);
    }
    else if (type === "contact") {
      setContact(val);
    }
    else if (type === "name") {
      setName(val);
    }
    else if (type === "role") {
      setRole(val);
    }
  }

  const registerUser = () => {
    // Generating a random password for the new user to be registered
    var pass = generator.generate({
      length: 8,
      numbers: true,
    })
    setPassword(pass);

    // A quick validation for the inputs provided by the admin
    if (name === "" || contact === "" || role === "" || !validEmail(email) || pass === "") {
      setMsg("Please enter valid details!");
      return;
    }

    try {
      // Register the new user
      firebase.auth().createUserWithEmailAndPassword(email, pass);

      // send the mail to the user regarding the registration
      SendMail(name, email, pass);
    }
    catch (err) {
      setMsg("Something went wrong!")
      console.log("Error while registration of the users", err);
    }
  }

  disappear();

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            Hermi
          </Typography>
          <Button onClick={handleClickOpen} color="inherit">Register</Button>
          <div>
            <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title" fullWidth={true}>
              <DialogTitle id="form-dialog-title">Register New User</DialogTitle>
              <Typography variant="h6" style={{ color: "#ff0000", fontSize: 14 }}>
                {msg}
              </Typography>
              <DialogContent>
                <TextField
                  variant="outlined"
                  autoFocus
                  label="Name"
                  fullWidth
                  style={{ marginBottom: 10 }}
                  onChange={e => setValue(e, "name")}
                />
                <TextField
                  variant="outlined"
                  label="Email Address"
                  type="email"
                  fullWidth
                  style={{ marginBottom: 10 }}
                  onChange={e => setValue(e, "email")}
                />
                <TextField
                  variant="outlined"
                  label="Contact Number"
                  type="number"
                  fullWidth
                  style={{ marginBottom: 10 }}
                  onChange={e => setValue(e, "contact")}
                />

                <FormControl variant="outlined" fullWidth>
                  <InputLabel id="demo-simple-select-outlined-label">Role</InputLabel>
                  <Select
                    labelId="demo-simple-select-outlined-label"
                    id="demo-simple-select-outlined"
                    value={role}
                    onChange={e => setValue(e, "role")}
                    label="Role"
                    fullWidth
                  >
                    <MenuItem value={"Admin"}>Admin</MenuItem>
                    <MenuItem value={"Inspector"}>Inspector</MenuItem>
                    <MenuItem value={"Custodian"}>Custodian</MenuItem>
                    <MenuItem value={"Custodian"}>Viewer</MenuItem>
                  </Select>
                </FormControl>
              </DialogContent>
              <DialogActions>
                <Button onClick={handleClose} color="primary">
                  Cancel
          </Button>
                <Button onClick={handleClose} color="primary">
                  Register
          </Button>
              </DialogActions>
            </Dialog>
          </div>
        </Toolbar>
      </AppBar>
    </div>
  );
}
