import SG_KEY from '../SendGridKey';

const sgMail = require('@sendgrid/mail');
sgMail.setApiKey(SG_KEY);

export const SendMail = (name, email, password) => {
    const msg = {
        to: 'ritikjain1272@gmail.com', // Change to your recipient
        from: '18bcs045@ietdavv.edu.in', // Change to your verified sender
        subject: 'Important information regarding your registration in Hirmi',
        text: `Hello ${name}, you have been recently been added to the hirmi project. You can now login with the following credentials: `,
        html: `<strong>Email: ${email}</strong><br/><strong>Password: ${password}`,
    };

    sgMail.send(msg)
    .then(() => console.log('Email sent to ', email))
    .catch(err => console.log('Error sending mail to the user', err));
}